.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 3rem;
  z-index: 100;
}
.header .logo {
  color: white;
  letter-spacing: 5px;
  font-size: 20px;
}
.header .nav-links {
  display: flex;
  justify-content: space-between;
  text-align: center;
  font-weight: 600;
  gap: 1rem;
}
.header .nav-links* {
  box-sizing: border-box;
  transition: all 0.35s ease;
}
.header .nav-links li {
  display: inline-block;
  list-style: outside none none;
}
.header .nav-links a {
  color: white;
  position: relative;
  text-decoration: none;
  font-size: 1rem;
  letter-spacing: 10px;
  z-index: 9;
}
.header .nav-links a:hover {
  font-size: 1rem !important;
}
.header .nav-active {
  transform: translateX(0%);
}
.header #hamburguesa {
  display: none;
  cursor: pointer;
}
.header #hamburguesa div {
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 5px;
  transition: all 0.3s ease;
}
.header #hamburguesa .linea1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.header #hamburguesa .linea2 {
  opacity: 0;
}
.header #hamburguesa .linea3 {
  transform: rotate(45deg) translate(-5px, -6px);
}
.header .neon-link::before {
  pointer-events: none;
  content: "";
  position: absolute;
  background: rgb(42, 182, 199);
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  transform: perspective(1em) rotateX(40deg) scale(0.8, 0.3);
  filter: blur(1em);
  opacity: 0.7;
}
.header .neon-link::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 2em 0.5em rgb(42, 182, 199);
  opacity: 0;
  background-color: rgb(42, 182, 199);
  z-index: -1;
  transition: opacity 100ms linear;
}

@media screen and (max-width: 768px) {
  .header {
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    position: fixed;
    padding: 1.5rem 1.5rem !important;
  }
}
@media screen and (max-width: 426px) {
  .header {
    padding: 1rem 1rem !important;
  }
  .nav-links {
    position: absolute;
    right: 0px;
    top: 8vh;
    height: 92vh;
    width: 100% !important;
    background: rgba(4, 4, 4, 0.911);
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    flex-direction: column;
    align-items: center;
    justify-content: space-around !important;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
  }
  .nav-links li {
    opacity: 0;
  }
  #hamburguesa {
    display: block !important;
  }
}
@-webkit-keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
.links-redes {
  display: flex;
