.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 3rem;
  overflow: hidden;
}
.container .glitch-img {
  overflow: hidden;
  background-position: center !important;
}

.container-about {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.div-about {
  color: white;
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9;
}

.div-about p {
  border: 1px solid;
  width: 100%;
  height: 60%;
  font-size: calc(0.6rem + 1.2vw) !important;
  padding: 1.5rem;
  text-align: left;
}

.container-about h1 {
  color: rgba(255, 255, 255, 0.237);
  font-size: clamp(3rem, 8vw, 6rem);
  position: absolute;
  top: 12px;
  left: -74px;
}

@media screen and (max-width: 800px) {
  .container .glitch-img::before, .container .glitch-img::after,
.container .glitch-img .channel {
    background-size: 110% !important;
    background-position: center;
  }
}
@media screen and (max-width: 768px) {
  .container {
    padding: 1.5rem 1.5rem;
  }
  .div-about p {
    padding: 1rem;
  }
  .container-about h1 {
    top: 68px;
  }
}
@media screen and (max-width: 700px) {
  .container-about {
    width: 100% !important;
    padding: 1.5rem;
  }
  .container-about h1 {
    left: 0;
  }
  .container .glitch-img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
  }
  .container .glitch-img::before, .container .glitch-img::after,
.container .glitch-img .channel {
    background-size: 70% !important;
    background-position: bottom;
  }
}
@media screen and (max-width: 426px) {
  .container-about {
    padding: 1rem;
  }
}
.div-carousel {
  height: 600px;
  width: 600px;
  position: relative;
}
.div-carousel .glitch-img {
  border-radius: 50% !important;
  background-color: black;
  z-index: 98;
}
.div-carousel .glitch-img::before, .div-carousel .glitch-img::after,
.div-carousel .glitch-img .channel {
  background-image: inherit !important;
  border: transparent;
  overflow: hidden;
  border-radius: 50%;
  border: 1px solid transparent;
}
.div-carousel .div-absolute {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
}
.div-carousel .img0 {
  border-radius: 50%;
  width: 33.3%;
  height: 33.3%;
  z-index: 9;
}
.div-carousel .img1 {
  border-radius: 50%;
  width: 66.6%;
  height: 66.6%;
  z-index: 8;
}
.div-carousel .img2 {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  z-index: 7;
}

#previus,
#next {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 20px;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 99999999;
  border: none;
  background: transparent;
  color: white;
  font-size: 2rem;
  cursor: pointer !important;
}

#previus {
  left: 0;
}

#next {
  right: 0;
}

.div-buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8rem;
  position: absolute;
  bottom: 100px;
  z-index: 99;
}
.div-buttons .neon-button {
  font-size: 1rem;
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  color: rgb(42, 182, 199);
  border: rgb(42, 182, 199) 0.125em solid;
  padding: 0.25em 1em;
  border-radius: 0.25em;
  text-shadow: 0 0 0.125em rgba(255, 255, 255, 0.3), 0 0 0.45em currentColor;
  box-shadow: inset 0 0 0.5em 0 rgb(42, 182, 199), 0 0 0.5em 0 rgb(42, 182, 199);
  position: relative;
}
.div-buttons .neon-button::before {
  pointer-events: none;
  content: "";
  position: absolute;
  background: rgb(42, 182, 199);
  top: 120%;
  left: 0;
  width: 100%;
  height: 100%;
  transform: perspective(1em) rotateX(40deg) scale(1, 0.35);
  filter: blur(1em);
  opacity: 0.3;
}
.div-buttons .neon-button::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 2em 0.5em rgb(42, 182, 199);
  opacity: 0;
  background-color: rgb(42, 182, 199);
  z-index: -1;
  transition: opacity 100ms linear;
}
.div-buttons .neon-button:hover {
  color: rgb(48, 32, 43);
  text-shadow: none;
}
.div-buttons .neon-button:hover::before {
  opacity: 0.8;
}
.div-buttons .neon-button:hover::after {
  opacity: 0.8;
}

@media screen and (max-width: 1200px) {
  .div-carousel {
    bottom: -50px;
  }
  .div-buttons {
    bottom: 80px;
  }
}
@media screen and (max-width: 690px) {
  .div-carousel {
    width: 500px !important;
    height: 500px !important;
  }
  .div-buttons {
    bottom: 80px;
  }
  .div-buttons .neon-button {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 590px) {
  .div-carousel {
    width: 300px !important;
    height: 300px !important;
    bottom: 0;
  }
  #previus,
#next {
    font-size: 1.5rem;
  }
  .div-buttons {
    bottom: 40px;
    gap: 5rem;
  }
}
