.circle {
  position: fixed;
  width: 60px;
  height: 60px;
  z-index: 110;
  filter: url(#wavy) blur(1px);
}
.circle:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border: 1px solid #fff;
  border-radius: 50%;
  box-shadow: 0 0 1px #0f0, inset 0 0 5px #0f0;
  -webkit-animation: animate 5s linear infinite;
          animation: animate 5s linear infinite;
}

@-webkit-keyframes animate {
  0% {
    box-shadow: 0 0 10px #0f0, inset 0 0 10px #0f0;
    filter: hue-rotate(0deg);
  }
  50% {
    box-shadow: 0 0 16px #0f0, inset 0 0 16px #0f0;
  }
  40% {
    box-shadow: 0 0 10px #0f0, inset 0 0 10px #0f0;
  }
  60% {
    box-shadow: 0 0 20px #0f0, inset 0 0 20px #0f0;
  }
  80% {
    box-shadow: 0 0 24px #0f0, inset 0 0 24px #0f0;
  }
  100% {
    box-shadow: 0 0 12px #0f0, inset 0 0 12px #0f0;
    filter: hue-rotate(360deg);
  }
}

@keyframes animate {
  0% {
    box-shadow: 0 0 10px #0f0, inset 0 0 10px #0f0;
    filter: hue-rotate(0deg);
  }
  50% {
    box-shadow: 0 0 16px #0f0, inset 0 0 16px #0f0;
